import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Slide from '@material-ui/core/Slide'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography'

import { Document, Page, pdfjs } from 'react-pdf'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import printJS from 'print-js'

import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { setPdf } from 'Pages/Protected/Store/actions'

// Styles
import classes from './PdfViewer2.module.scss'

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`

const mozillaPdfViewer = 'https://mozilla.github.io/pdf.js/web/viewer.html'
// const mozillaPdfViewer = 'pdfs'

const Transition = React.forwardRef(function Transition (props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})

let dispatch = null

const PdfView2Component = props => {
  // Variables
  const { name, fileName, initialPageNumber = 1, initialScale, enabled } = props
  // Service
  const { service } = props

  const [debounce, setDebounce] = React.useState(null)

  const [file, setFile] = React.useState()
  const [numPages, setNumPages] = React.useState(0)
  const [scale, setScale] = React.useState(initialScale)
  const [pageNumber, setPageNumber] = React.useState(initialPageNumber)
  const [searchText, setSearchText] = React.useState('')

  React.useEffect(() => {
    setFile()
    setPageNumber(initialPageNumber)
    dispatch(setPdf({ enabled: true }))

    if (service) {
      service.then(response => {
        const _blob = new Blob([response.data])
        setFile(_blob)
      })
    }
  }, [service])

  const download = () => {
    const url = URL.createObjectURL(new Blob([file]))
    const link = document.createElement('a')
    link.href = url
    link.setAttribute('download', `${fileName}.pdf`)
    document.body.appendChild(link)
    link.click()
  }

  const print = async () => {
    try {
      const { ipcRenderer } = window.require('electron')
      const data = await blobToBase64(file)
      ipcRenderer.send('print-to-pdf', { data, fileName })

      dispatch(setPdf({ enabled: false }))
      ipcRenderer.on('print-to-pdf-close', event => {
        dispatch(setPdf({ enabled: true }))
      })
    } catch (error) {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onloadend = function () {
        const dataUrl = reader.result
        const printable = dataUrl.split(',')[1]
        printJS({ printable, type: 'pdf', base64: true })
      }
    }
  }

  const blobToBase64 = blob => {
    return new Promise(resolve => {
      const reader = new FileReader()
      reader.onload = function () {
        const dataUrl = reader.result
        const base64 = dataUrl.split(',')[1]
        resolve(base64)
      }
      reader.readAsDataURL(blob)
    })
  }

  const handleOnSearch = event => {
    const search = event.target.value
    clearTimeout(debounce)
    setDebounce(
      setTimeout(() => {
        console.log('heelo')
        setSearchText(search)
      }, 500)
    )
  }

  return (
    <div className={classes.root}>
      <div className={classes.controls}>
        <Typography id={'pdf-preview-title'} className={classes['file-name']} align='left'>
          {name}
        </Typography>
        <Tooltip title='Search text'>
          <span>
            <div className={classes.search}>
              <SearchTextComponent onChange={handleOnSearch} />
            </div>
          </span>
        </Tooltip>

        <Typography className={classes['page-number']}>Page: {pageNumber}</Typography>
        <Tooltip title='Previous Page' className={`${classes.control} ${numPages <= 1 ? classes.disabled : ''}`}>
          <span>
            <IconButton
              id={'pdf-preview-page-previous'}
              disabled={numPages <= 1}
              onClick={() => {
                const page = pageNumber > 1 ? pageNumber - 1 : pageNumber
                setPageNumber(page)
                const elem = document.getElementById(`pdf-page-${page}`)
                console.log(elem)
                elem.scrollIntoView()
              }}
            >
              <FontAwesomeIcon icon='arrow-alt-circle-left' className={classes.icon} />
            </IconButton>
          </span>
        </Tooltip>

        <Tooltip title='Next Page' className={`${classes.control} ${numPages <= 1 ? classes.disabled : ''}`}>
          <span>
            <IconButton
              id={'pdf-preview-page-next'}
              disabled={numPages <= 1}
              onClick={() => {
                const page = pageNumber < numPages ? pageNumber + 1 : pageNumber
                setPageNumber(page)
                const elem = document.getElementById(`pdf-page-${page}`)
                elem.scrollIntoView()
              }}
            >
              <FontAwesomeIcon icon='arrow-alt-circle-right' className={classes.icon} />
            </IconButton>
          </span>
        </Tooltip>

        <Tooltip title='Zoom in' className={classes.control}>
          <span>
            <IconButton id={'pdf-preview-zoom-in'} onClick={() => setScale(scale + 0.2)}>
              <FontAwesomeIcon icon='search-plus' className={classes.icon} />
            </IconButton>
          </span>
        </Tooltip>

        <Tooltip title='Zoom out' className={classes.control}>
          <span>
            <IconButton onClick={() => (scale === 0.6 ? '' : setScale(scale - 0.2))}>
              <FontAwesomeIcon icon='search-minus' className={classes.icon} />
            </IconButton>
          </span>
        </Tooltip>

        <Tooltip title='Print' className={`${classes.control} ${!enabled ? classes.disabled : ''}`}>
          <span>
            <IconButton disabled={!enabled} onClick={print}>
              <FontAwesomeIcon icon='print' className={classes.icon} />
            </IconButton>
          </span>
        </Tooltip>

        <Tooltip title='Download' className={`${classes.control} ${!enabled ? classes.disabled : ''}`}>
          <span>
            <IconButton disabled={!enabled} onClick={download}>
              <FontAwesomeIcon icon='save' className={classes.icon} />
            </IconButton>
          </span>
        </Tooltip>
      </div>

      <div className={classes.viewport}>
        <div className={classes.pdf}>
          <Document
            file={file}
            onLoadSuccess={({ numPages }) => {
              setNumPages(numPages)
            }}
          >
            {[...Array(numPages)]
              .map((value, index) => index + 1)
              .map((value, index) => (
                <div className={classes.page} id={`pdf-page-${value}`} key={`pdf-page-${value}`}>
                  <Page
                    pageNumber={value}
                    scale={scale}
                    renderMode={'canvas'}
                    customTextRenderer={textItem =>
                      textItem.str
                        .split(searchText)
                        .reduce(
                          (strArray, currentValue, currentIndex) =>
                            currentIndex === 0
                              ? [...strArray, currentValue]
                              : [...strArray, <mark key={currentIndex}>{searchText}</mark>, currentValue],
                          []
                        )
                    }
                  />
                </div>
              ))}
          </Document>
        </div>
      </div>
    </div>
  )
}

const MozillaPdf = props => {
  // Variables
  const { name } = props
  // Service
  const { service } = props

  const [url, setUrl] = React.useState('')

  React.useEffect(() => {
    setUrl()
    if (service) {
      service.then(response => {
        const _blob = new Blob([response.data], { type: 'application/pdf' })
        const blobUrl = URL.createObjectURL(_blob)
        const _url = encodeURIComponent(blobUrl)

        // const pdfArray = convertDataURIToBinary('data:application/pdf;base64, ' + _blob)

        // const PdfViewer = document.getElementById('pdfViewer')
        // if (PdfViewer) {
        //   PdfViewer.contentWindow.PDFViewerApplication.open(pdfArray, 1.5)
        //   setUrl(_url)
        // }

        blobToBase64(_blob).then(data => {
          const pdfAsDataUri = 'data:application/pdf;base64,' + data
          const pdfArray = convertDataURIToBinary(pdfAsDataUri)
          setUrl(pdfAsDataUri)
          console.log(pdfAsDataUri)
          const PdfViewer = document.getElementById('pdfViewer')
          if (PdfViewer) {
            PdfViewer.contentWindow.PDFViewerApplication.open(pdfArray, 1.5)
            setUrl(_url)
          }
        })
      })
    }
  }, [service])

  const convertDataURIToBinary = blob => {
    const BASE64_MARKER = ';base64,'
    var base64Index = blob.indexOf(BASE64_MARKER) + BASE64_MARKER.length
    var base64 = blob.substring(base64Index)
    var raw = window.atob(base64)
    var rawLength = raw.length
    var array = new Uint8Array(new ArrayBuffer(rawLength))

    for (var i = 0; i < rawLength; i++) {
      array[i] = raw.charCodeAt(i)
    }
    return array
  }

  const blobToBase64 = blob => {
    return new Promise(resolve => {
      const reader = new FileReader()
      reader.onload = function () {
        const dataUrl = reader.result
        const base64 = dataUrl.split(',')[1]
        resolve(base64)
      }
      reader.readAsDataURL(blob)
    })
  }

  return (
    <div className={classes.root}>
      {url && (
        <iframe
          // id={'pdf-viewer'}
          className={classes.iframe}
          src={`${mozillaPdfViewer}`}
          width='100%'
          height='100%'
        />
      )}
    </div>
  )
}

const MozillaPdfURL = props => {
  // Variables
  const { name } = props
  // Service
  const { service } = props

  const [url, setUrl] = React.useState('')

  React.useEffect(() => {
    setUrl()
    if (service) {
      service.then(response => {
        const _url = encodeURIComponent(response)
        setUrl(_url)
        // }
      })
    }
  }, [service])

  return (
    <div className={classes.root}>
      <iframe
        // id={'pdf-viewer'}
        className={classes.iframe}
        src={`${mozillaPdfViewer}?file=${url}`}
        width='100%'
        height='100%'
      />
    </div>
  )
}

const PdfViewer2Modal = () => {
  dispatch = useDispatch()
  const {
    open = false,
    name = '',
    fileName = '',
    initialPageNumber = 1,
    initialScale = 1,
    service,
    onClose,
    enabled
  } = useSelector(state => state.pdf)

  const handleOnClose = () => {
    if (!enabled) return

    if (onClose) onClose()
    dispatch(setPdf({ open: false, service: null, onClose: null }))
  }

  return (
    <Dialog id={'pdf-preview'} fullScreen open={open} onClose={handleOnClose} TransitionComponent={Transition}>
      <AppBar style={{ position: 'relative' }}>
        <Toolbar>
          <IconButton id={'pdf-preview-close'} edge='start' color='inherit' onClick={handleOnClose} aria-label='close'>
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' className={classes.title}>
            Preview Sheet
          </Typography>
        </Toolbar>
      </AppBar>
      <DialogContent style={{ padding: '0px' }}>
        {PdfView2Component({
          name,
          fileName,
          initialPageNumber,
          initialScale,
          service,
          enabled
        })}
      </DialogContent>
    </Dialog>
  )
}

const PdfViewer2Service = options => dispatch(setPdf(options))

export { PdfViewer2Service, PdfViewer2Modal, PdfView2Component, MozillaPdf, MozillaPdfURL }
