import React from 'react'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
// import ClaimsEncodingServices from 'Services/Api/Claims/ClaimsEncodingServices'
import CustomerCareServices from 'Services/Api/Claims/CustomerCareServices'
import { PdfViewer2Service } from 'SharedComponents/PdfViewer2/PdfViewer2'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { ClaimsEncoding as actions } from 'Pages/Protected/Claims/Store/actions'

import GetClaimComponent from './GetClaimComponent'

// Utilities
import Total from 'Utilities/Total'

// Libraries
import moment from 'moment'
// Validators
import DiagnosisListValidator from './Components/DiagnosisList/DiagnosisListValidator'
import HospitalItemValidator from './Components/HospitalItemForm/HospitalItemFormValidator'
import ProfessionalFeeValidator from './Components/ProfessionalFeeForm/ProfessionalFeeFormValidator'
import ProcedureValidator from './Components/ProcedureForm/ProcedureFormValidator'
import DentalProcedureValidator from './Components/DentalProcedureForm/DentalProcedureFormValidator'
import DentalProcedureDiagnosisValidator from './Components/DentalProcedureForm/DiagnosisValidator'
import ProcedureDiagnosisValidator from './Components/ProcedureForm/DiagnosisValidator'
import HospitalItemDiagnosisValidator from './Components/HospitalItemForm/DiagnosisValidator'
import ProfessionalFeeDiagnosisValidator from './Components/ProfessionalFeeForm/DiagnosisValidator'
import { SoftValidatorService, SoftValidatorAlertComponent } from './Components/BaseDetails/SoftValidator'

const {
  resetClaim,
  setGrandTotal,
  setClaimType,
  setSuggestion,
  resetSuggestion,
  setClaimFacility,
  setClaimDoctor,
  setProcedure,
  setDentalProcedure,
  setHospitalItem,
  setProfessionalFee,
  setError,
  resetError,
  // addSuggestionToArray,
  resetGrandTotal,
  setClaimPro,
  setDropDown
} = actions

export default function ClaimPage (props) {
  const { apiService } = props
  const dispatch = useDispatch()
  const claim = useSelector(({ ClaimsEncoding: state }) => state.claim)
  const claim_type = useSelector(({ ClaimsEncoding: state }) => state.claimType)
  // const suggestions = useSelector(({ ClaimsEncoding: state }) => state.suggestions)
  const error = useSelector(({ ClaimsEncoding: state }) => state.error)

  // Line Item Form Store
  const _dentalProcedure = useSelector(({ ClaimsEncoding: state }) => state.dentalProcedure)
  const _procedure = useSelector(({ ClaimsEncoding: state }) => state.procedure)
  const _hospitalItem = useSelector(({ ClaimsEncoding: state }) => state.hospitalItem)
  const _professionalFee = useSelector(({ ClaimsEncoding: state }) => state.professionalFee)

  const getBatchDetails = ({ _batchNumber }) => {
    apiService.getBatch({ batch_number: _batchNumber }).then(res => {
      const { data } = res.data
      dispatch(setClaimType(data.claim.type))
      LoadingService.loading(false)
    })
  }

  React.useEffect(() => {
    dispatch(resetClaim())
    dispatch(resetError())
    LoadingService.loading(true)
    const { _batchNumber, _claimNumber } = props.match.params

    let pageTitle = ''
    let breadCrumbs = ['dashboard', 'batches', `specificBatch:${_batchNumber}`]
    if (props.isAdd) {
      pageTitle = 'Add new claim'
      breadCrumbs = [...breadCrumbs, 'addClaim']
    } else {
      pageTitle = `Update Claim No. ${_claimNumber}`
      breadCrumbs = [...breadCrumbs, 'updateClaim']
    }

    PageTitleService(pageTitle)
    BreadcrumbsService.setSelectedItems(breadCrumbs)
    getBatchDetails({ _batchNumber })

    return () => {
      dispatch(setClaimType(''))
      dispatch(resetClaim())
      dispatch(resetGrandTotal())
    }
  }, [props.match.params._batchNumber])

  React.useEffect(() => {
    const grand = Total.Grand(claim)
    dispatch(setGrandTotal(grand))
  }, [claim])

  React.useEffect(() => {
    if (error) {
      console.group('Validation Failed')
      for (const value in error) {
        console.log(value, error[value])
      }
      console.groupEnd()
    }
  }, [error])

  const [debounce, setDebounce] = React.useState({
    facility: null,
    doctor: null,
    icd10: null,
    cpt: null,
    hospital_items: null,
    pro: null
  })

  const [fetching, setFetching] = React.useState({
    facility_id: false,
    facility_name: false,
    doctor_id: false,
    doctor_name: false,
    pro_id: false,
    pro_name: false,
    anesthesiologist_id: false,
    anesthesiologist_name: false
  })

  const getClaimDetails = callback => {
    const { _batchNumber, _claimNumber } = props.match.params
    apiService
      .getClaim({
        batch_number: _batchNumber,
        claim_number: _claimNumber
      })
      .then(res => {
        const { data } = res.data
        // Set Diagnosis Suggestions
        // const { diagnosis = [] } = data
        // diagnosis.map(i => dispatch(addSuggestionToArray({ field: 'icd10' })))

        callback(data)
      })
  }

  //* need to refector if to add addt'l default
  //* this only support facility
  const initDefault = () => {
    apiService.getBatch({ batch_number: props.match.params._batchNumber }).then(res => {
      const { data } = res.data
      dispatch(setClaimFacility({ facility_id: data.facility.id, facility_name: data.facility.name }))
      apiService.lookups({
        on: 'medical-partner',
        params: { query: data.facility.id, by: 'id' }})
        .then(response => {
          const [facility] = response.data.data
          const rooms = (facility.rooms || []).map(item => ({
            text: item.desc,
            value: item.value,
            rate: item.rate || 0
          }))
          dispatch(setDropDown({ rooms }))
        })
    })
  }

  //* Facility
  const handleOnChangeFacility = ({ value, field, search }) => {
    if (search) {
      dispatch(resetSuggestion({ field: 'facility' }))
      dispatch(setClaimFacility({ [field]: value }))
      const { 1: by } = field.split('_')
      getFacilitySuggestions({ value, by, field })
    } else {
      const _value = value || {}
      dispatch(resetSuggestion({ field: 'facility' }))
      dispatch(setClaimFacility({ facility_id: _value.id, facility_name: _value.name }))
      return _value.rooms
    }
  }

  const getFacilitySuggestions = ({ value = '', by, field }) => {
    if (value.trim().length < 3) return

    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.facility)
    setDebounce(state => ({
      ...state,
      facility: setTimeout(() => {
        if (value) {
          apiService
            .lookups({
              on: 'medical-partner',
              params: { query: value, by }
            })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'facility',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }

  //* Doctor
  const handleOnChangeDoctor = ({ value, field, search }) => {
    if (search) {
      dispatch(setClaimDoctor({ [field]: value }))
      dispatch(resetSuggestion({ field: 'doctor' }))
      const { 1: by } = field.split('_')
      let _by = by === 'id' ? 'id' : 'full_name'
      getDoctorSuggestions({ value, by: _by, field })
    } else {
      const _value = value || {}
      dispatch(setClaimDoctor({ doctor_id: _value.id, doctor_name: _value.full_name }))
      dispatch(resetSuggestion({ field: 'doctor' }))
    }
  }

  const getDoctorSuggestions = ({ value = '', by, field }) => {
    if (value.trim().length < 3) return

    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.doctor)
    setDebounce(state => ({
      ...state,
      doctor: setTimeout(() => {
        if (value) {
          apiService
            .lookups({
              on: 'doctor-partner',
              params: { query: value, by }
            })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'doctor',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }

  // PRO
  const handleOnChangePro = ({ value, field, search }) => {
    if (search) {
      dispatch(setClaimPro({ [field]: value }))
      dispatch(resetSuggestion({ field: 'pro' }))
      getProSuggestions({ value, field })
    } else {
      const _value = value || {}
      dispatch(setClaimPro({ pro_name: _value.name, pro_id: _value.employee_id }))
      dispatch(resetSuggestion({ field: 'pro' }))
    }
  }

  const getProSuggestions = ({ value, field }) => {
    if (value.trim().length < 3) return

    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.pro)
    setDebounce(state => ({
      ...state,
      pro: setTimeout(() => {
        if (value) {
          // PRO User lookup
          CustomerCareServices.proUsersLookup({ value, field })
            .then(res => {
              // limit temporary - no limit in api seer
              const limit = []
              for (let i = 0; i < 10; i++) {
                if (!res.data.data[i]) break
                limit.push(res.data.data[i])
              }

              const data = limit.map(elem => {
                let full_name = `${elem.first_name} ${elem.last_name}`
                delete elem.first_name
                delete elem.last_name
                return { ...elem, name: full_name }
              })

              dispatch(
                setSuggestion({
                  field: 'pro',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }

  // Anaesthesiologist
  const handleOnChangeAnaesthesiologist = ({ value, field, search }) => {
    if (search) {
      dispatch(setClaimDoctor({ [field]: value }))
      dispatch(resetSuggestion({ field: 'anesthesiologist' }))
      const { 1: by } = field.split('_')
      let _by = by === 'id' ? 'id' : 'full_name'
      getAnaesthesiologistSuggestions({ value, by: _by, field })
    } else {
      const _value = value || {}
      dispatch(setClaimDoctor({ anesthesiologist_id: _value.id, anesthesiologist_name: _value.full_name }))
      dispatch(resetSuggestion({ field: 'anesthesiologist' }))
    }
  }

  const getAnaesthesiologistSuggestions = ({ value = '', by, field }) => {
    if (value.trim().length < 3) return

    setFetching(state => ({ ...state, [field]: true }))
    clearTimeout(debounce.anaesthesiologist)
    setDebounce(state => ({
      ...state,
      doctor: setTimeout(() => {
        if (value) {
          apiService
            .lookups({
              on: 'doctor-partner',
              params: { query: value, by }
            })
            .then(res => {
              const { data } = res.data
              dispatch(
                setSuggestion({
                  field: 'anesthesiologist',
                  suggestion: data
                })
              )
            })
            .finally(() => {
              setFetching(state => ({ ...state, [field]: false }))
            })
        }
      }, 500)
    }))
  }

  // Report: Return Notice
  const handleOnViewClaimNotice = mode => {
    LoadingService.loading(true)
    const { _claimNumber } = props.match.params
    const pdf = {
      name: 'Letter of Advice',
      fileName: 'letter_of_advice',
      service: apiService.getLetterOfAdvice({ claimNo: _claimNumber })
    }
    PdfViewer2Service({ ...pdf, open: true })
    LoadingService.loading(false)
  }

  // Validation
  const doValidateAll = ({ validator, field = undefined, flag = false }) => {
    dispatch(resetError())
    const base_errors = doValidateBase({ claim, validator: validator, field, flag })
    let diagnosis = {}
    let procedure = {}
    let hospital_item = {}
    let professional_fee = {}
    if (claim.diagnosis) {
      diagnosis = doValidateDiagnosis({ diagnosis: claim.diagnosis })
    }

    if (_procedure.show || _dentalProcedure.show) {
      const error = flag
        ? doValidateDentalProcedure({ data: _dentalProcedure.details, index: _dentalProcedure.index, flag })
        : doValidateProcedure({ data: _procedure.details, index: _procedure.index })
      if (Object.keys(error).length > 0) {
        procedure = { ...error }
        if (flag) {
          dispatch(
            setDentalProcedure({
              show: true,
              mode: 'add',
              index: _dentalProcedure.index,
              details: _dentalProcedure.details
            })
          )
        } else {
          dispatch(
            setProcedure({
              show: true,
              mode: 'add',
              index: _procedure.index,
              details: _procedure.details
            })
          )
        }
      }
    } else if (claim.procedures) {
      for (const [index, value] of claim.procedures.entries()) {
        const error = flag
          ? doValidateDentalProcedure({ data: value, index, flag })
          : doValidateProcedure({ data: value, index })
        if (Object.keys(error).length > 0) {
          procedure = { index, ...error }
          // AUTO OPEN CLAIM
          const params = { show: true, mode: 'edit', index, details: value }
          flag ? dispatch(setDentalProcedure(params)) : dispatch(setProcedure(params))
          break
        }
      }
    }

    if (_hospitalItem.show) {
      const error = doValidateHospitalItem({ data: _hospitalItem.details, index: _hospitalItem.index, flag })
      if (Object.keys(error).length > 0) {
        hospital_item = { ...error }
        const params = { show: true, mode: 'add', index: _hospitalItem.index, details: _hospitalItem.details }
        dispatch(setHospitalItem(params))
      }
    } else if (claim.hospital_items) {
      for (const [index, value] of claim.hospital_items.entries()) {
        const error = doValidateHospitalItem({ data: value, index })
        if (Object.keys(error).length > 0) {
          hospital_item = { index, ...error }
          // AUTO OPEN CLAIM
          dispatch(
            setHospitalItem({
              show: true,
              mode: 'edit',
              index,
              details: value
            })
          )
          break
        }
      }
    }

    if (_professionalFee.show) {
      const error = doValidateProfessionalFee({ data: _professionalFee.details, index: _professionalFee.index, flag })
      if (Object.keys(error).length > 0) {
        professional_fee = { ...error }
        const params = { show: true, mode: 'add', index: _professionalFee.index, details: _professionalFee.details }
        dispatch(setProfessionalFee(params))
      }
    } else if (claim.professional_fees) {
      for (const [index, value] of claim.professional_fees.entries()) {
        const error = doValidateProfessionalFee({ data: value, index })
        if (Object.keys(error).length > 0) {
          professional_fee = { index, ...error }
          // AUTO OPEN CLAIM
          dispatch(
            setProfessionalFee({
              show: true,
              mode: 'edit',
              index,
              details: value
            })
          )
          break
        }
      }
    }

    const invalid =
      Object.keys(base_errors).length > 0 ||
      Object.keys(procedure).length > 0 ||
      Object.keys(hospital_item).length > 0 ||
      Object.keys(professional_fee).length > 0 ||
      diagnosis.error

    return invalid
  }

  const doValidateBase = ({ claim, validator, field, flag }) => {
    const data = {}
    const {
      diagnosis,
      procedures,
      hospital_items,
      professional_fees,
      reimbursement_info,
      reimbursement_info_analysts,
      ppe_packages,
      ...rest
    } = claim
    for (let f in rest) data[f] = claim[f]
    let _fields = {}
    if (field) _fields = { ...field, flag }
    else _fields = { ...claim, flag }
    const { error = {} } = validator(Object.keys(_fields))({ ...data, flag })
    dispatch(
      setError({
        ...error
      })
    )
    return error
  }

  const doValidateDiagnosis = ({ diagnosis }) => {
    const error = DiagnosisListValidator(diagnosis)
    dispatch(
      setError({
        diagnosis: error
      })
    )
    return error
  }

  const doValidateDentalProcedure = ({ data, index }) => {
    const { error } = DentalProcedureValidator({
      description: data.description,
      code: data.code.toString(),
      gross_amount: data.gross_amount,
      vat_amount: data.vat_amount,
      amount_paid_by_member: data.amount_paid_by_member,
      philhealth_amount_paid: data.philhealth_amount_paid,
      tooth_number: data.tooth_number
    })

    let diagnosisError = {}
    if (claim.diagnosis) {
      let exists = true
      if (
        !claim.diagnosis.find(
          item => item.code === data.diagnosis_code && item.description === data.diagnosis_description
        )
      ) {
        exists = false
        dispatch(setDentalProcedure({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
      diagnosisError = DentalProcedureDiagnosisValidator({
        diagnosis_description: exists ? data.diagnosis_description : '',
        diagnosis_code: exists ? data.diagnosis_code : '',
        diagnosis_given_diagnosis: exists ? data.diagnosis_given_diagnosis : ''
      })
    }

    dispatch(
      setError({
        procedure: { index, ...error, ...diagnosisError.error }
      })
    )

    return { ...error, ...diagnosisError.error }
  }

  const doValidateProcedure = ({ data, index }) => {
    const { error } = ProcedureValidator({
      description: data.description,
      code: data.code ? `${data.code}` : '',
      gross_amount: data.gross_amount,
      vat_amount: data.vat_amount,
      amount_paid_by_member: data.amount_paid_by_member,
      philhealth_amount_paid: data.philhealth_amount_paid
    })

    let diagnosisError = {}
    if (claim.diagnosis) {
      let exists = true
      if (
        !claim.diagnosis.find(
          item => item.code === data.diagnosis_code && item.description === data.diagnosis_description
        )
      ) {
        exists = false
        dispatch(setProcedure({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
      diagnosisError = ProcedureDiagnosisValidator({
        diagnosis_description: exists ? data.diagnosis_description : '',
        diagnosis_code: exists ? data.diagnosis_code : '',
        diagnosis_given_diagnosis: exists ? data.diagnosis_given_diagnosis : ''
      })
    }

    dispatch(
      setError({
        procedure: { index, show: true, ...error, ...diagnosisError.error }
      })
    )

    return { ...error, ...diagnosisError.error }
  }

  const doValidateHospitalItem = ({ data, index }) => {
    const { error } = HospitalItemValidator({
      name: data.name,
      item_code: data.item_code,
      gross_amount: data.gross_amount,
      vat_amount: data.vat_amount,
      amount_paid_by_member: data.amount_paid_by_member,
      philhealth_amount_paid: data.philhealth_amount_paid
    })
    let diagnosisError = {}
    if (claim.diagnosis) {
      let exists = true
      if (
        !claim.diagnosis.find(
          item => item.code === data.diagnosis_code && item.description === data.diagnosis_description
        )
      ) {
        exists = false
        dispatch(setHospitalItem({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
      diagnosisError = HospitalItemDiagnosisValidator({
        diagnosis_description: exists ? data.diagnosis_description : '',
        diagnosis_code: exists ? data.diagnosis_code : '',
        diagnosis_given_diagnosis: exists ? data.diagnosis_given_diagnosis : ''
      })
    }

    dispatch(
      setError({
        hospital_item: { index, ...error, ...diagnosisError.error }
      })
    )
    return { ...error, ...diagnosisError.error }
  }

  const doValidateProfessionalFee = ({ data, index }) => {
    const { error } = ProfessionalFeeValidator({
      doctor_id: data.doctor_id,
      doctor_name: data.doctor_name,
      specialization: data.specialization,
      gross_amount: data.gross_amount,
      vat_amount: data.vat_amount,
      amount_paid_by_member: data.amount_paid_by_member,
      philhealth_amount_paid: data.philhealth_amount_paid
    })
    let diagnosisError = {}
    if (claim.diagnosis) {
      let exists = true
      if (
        !claim.diagnosis.find(
          item => item.code === data.diagnosis_code && item.description === data.diagnosis_description
        )
      ) {
        exists = false
        dispatch(setProfessionalFee({ details: { diagnosis_code: '', diagnosis_description: '' } }))
      }
      diagnosisError = ProfessionalFeeDiagnosisValidator({
        diagnosis_description: exists ? data.diagnosis_description : '',
        diagnosis_code: exists ? data.diagnosis_code : '',
        diagnosis_given_diagnosis: exists ? data.diagnosis_given_diagnosis : ''
      })
    }
    dispatch(
      setError({
        professional_fee: { index, ...error, ...diagnosisError.error }
      })
    )
    return { ...error, ...diagnosisError.error }
  }

  const handleOnSave = async ({ lineItemFields, allowedFields, goBack = true }) => {
    const { _batchNumber, _claimNumber } = props.match.params
    const claimFields = Object.keys(allowedFields)

    if (claimFields.includes('approval_code') && claimFields.includes('loa_number')) {
      const proceed = await SoftValidatorService({
        fields: [{ 'Approval Code': claim.approval_code }, { 'LOA No.': claim.loa_number }]
      })
      if (!proceed) return
    }

    LoadingService.loading(true)
    const data = {}
    for (let f in { ...allowedFields, ...lineItemFields }) data[f] = claim[f]

    if (!data.date_to) data.date_to = moment().toISOString()

    if (props.isAdd) {
      apiService.addClaim({ batch_number: _batchNumber, data }).then(res => {
        LoadingService.loading(false)
        if (goBack) return props.history.push(`/batches/${_batchNumber}`)
        props.history.push(`/`)
      })
    } else {
      apiService.updateClaim({ batch_number: _batchNumber, claim_number: _claimNumber, data }).then(res => {
        LoadingService.loading(false)
        if (goBack) return props.history.push(`/batches/${_batchNumber}`)
        props.history.push(`/`)
      })
    }
  }

  return (
    <React.Fragment>
      {claim_type && (
        <GetClaimComponent
          _type={claim_type}
          {...props}
          claim={claim}
          fetching={fetching}
          onChangeFacility={handleOnChangeFacility}
          getClaimDetails={getClaimDetails}
          onSave={handleOnSave}
          onChangePro={handleOnChangePro}
          onChangeDoctor={handleOnChangeDoctor}
          onChangeAnaesthesiologist={handleOnChangeAnaesthesiologist}
          doViewClaimNotice={handleOnViewClaimNotice}
          doValidateAll={doValidateAll}
          doValidateDiagnosis={doValidateDiagnosis}
          doValidateProcedure={doValidateProcedure}
          doValidateDentalProcedure={doValidateDentalProcedure}
          doValidateHospitalItem={doValidateHospitalItem}
          doValidateProfessionalFee={doValidateProfessionalFee}
          initDefault={initDefault}
        />
      )}
      <SoftValidatorAlertComponent />
    </React.Fragment>
  )
}
